import React from 'react'

import styles from './Games.module.css'
import { NavLink } from 'react-router-dom'

import imgCheckCorrect from './../../assets/img/check-the-correct-one.svg'
import imgSelectTranslation from './../../assets/img/select-translation.svg'
import imgSprintGuess from './../../assets/img/sprint-guess.svg'
import imgPutTranslation from './../../assets/img/put-translation.svg'
import imgSpeakAndCheck from './../../assets/img/speak-and-check.svg'
import imgSprintListen from './../../assets/img/listen-sprint.svg'
import imgRememberTranslation from './../../assets/img/remember-translation.svg'
import imgWriteTranslation from './../../assets/img/write-translation.svg'
import imgListenAndGuess from './../../assets/img/listen-and-guess.svg'

const Games = () => {

    const GAMES = [
        {img: imgCheckCorrect, path: 'check-it', name: 'Check the correct one', describtion: 'Say the word on the screen and check your spelling'},
        {img: imgWriteTranslation, path: 'write-it', name: 'Write the translation', describtion: 'Say the word on the screen and check your spelling'},
        {img: imgPutTranslation, path: 'select-right', name: 'Put together a translation', describtion: 'Say the word on the screen and check your spelling'},
        {img: imgSelectTranslation, path: 'check-it', name: 'Select the right translation ', describtion: 'Say the word on the screen and check your spelling'},
        {img: imgSprintGuess, path: 'sprint-guess', name: 'Sprint by guessing', describtion: 'Say the word on the screen and check your spelling'},
        {img: imgSpeakAndCheck, path: 'check-it', name: 'Speak and check ', describtion: 'Say the word on the screen and check your spelling'},
        {img: imgSprintListen, path: 'check-it', name: 'Sprint by listen', describtion: 'Say the word on the screen and check your spelling'},
        {img: imgRememberTranslation, path: 'check-it', name: 'Remember translation', describtion: 'Say the word on the screen and check your spelling'},
        {img: imgListenAndGuess, path: 'check-it', name: 'Guess and listen', describtion: 'Say the word on the screen and check your spelling'}
    ]

    return (
        <section className={styles.gameContainer}>
            {GAMES.map((game, index) => (
                <NavLink key={index} to={game.path}>
                    <div className={styles.gameBlock}>
                        <div>
                            <h2>{game.name}</h2>
                            <p>{game.describtion}</p>
                        </div>

                        <img src={game.img} alt="" />
                    </div>
                </NavLink>
            ))}
        </section>
    )
}
export default Games